
import { defineComponent, ref } from 'vue';

export default defineComponent({
  name: 'MetaUserImpersonate',
  props: {
    meta: Object
  },
  methods: {
    handleChange(event: any) {
      console.log(event);
      return this.$emit('meta-change', {
        detail: {
          name: 'meta',
          value: {
            ...this.meta,
            [event.detail.name]: event.detail.value
          }
        }
      });
    }
  }
});
