
import { computed, defineComponent, ref } from 'vue';
import { useStore } from 'vuex';
import MetaIndicator from './MetaIndicator.vue';
import MetaOptions from './MetaOptions.vue';
import MetaRelation from './MetaRelation.vue';
import MetaMultiple from './MetaMultiple.vue';
import MetaUserImpersonate from './MetaUserImpersonate.vue';
import MetaPercentage from './MetaPercentage.vue';

export default defineComponent({
  name: 'FieldView',
  data() {
    return {
      isExpanded: false
    };
  },
  props: {
    field: Object
  },
  components: {
    MetaOptions,
    MetaIndicator,
    MetaRelation,
    MetaUserImpersonate,
    MetaMultiple,
    MetaPercentage
  },
  methods: {
    handleExpand() {
      this.isExpanded = true;
    },
    handleCollapse() {
      this.isExpanded = false;
    },
    handleChange(event: any) {
      this.$emit('change', {
        id: this.field?.id,
        name: event.detail.name,
        value: event.detail.value
      });
    }
  }
});
